import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import axios from 'axios';
import io from 'socket.io-client';
import ItineraryTab from './tabs/ItineraryTab';
import ExpensesTab from './tabs/ExpensesTab';
import BalancesTab from './tabs/BalancesTab';
import VotingTab from './tabs/VotingTab';
import DocumentsTab from './tabs/DocumentsTab';
import './TripView.css';

const API_URL = 'http://localhost:5000/api';
const SOCKET_URL = 'http://localhost:5000';

function TripView({ currentUser, onLogout }) {
  const { tripId } = useParams();
  const navigate = useNavigate();
  const [trip, setTrip] = useState(null);
  const [members, setMembers] = useState([]);
  const [activeTab, setActiveTab] = useState('itinerary');
  const [loading, setLoading] = useState(true);
  const [socket, setSocket] = useState(null);
  const [isOffline, setIsOffline] = useState(false);

  useEffect(() => {
    // Check offline status
    const handleOnline = () => setIsOffline(false);
    const handleOffline = () => setIsOffline(true);
    
    window.addEventListener('online', handleOnline);
    window.addEventListener('offline', handleOffline);
    setIsOffline(!navigator.onLine);

    // Initialize socket connection
    const newSocket = io(SOCKET_URL);
    newSocket.on('connect', () => {
      console.log('Connected to server');
      newSocket.emit('join-trip', tripId);
    });

    setSocket(newSocket);

    // Socket event listeners
    newSocket.on('activity-added', (activity) => {
      // Handle real-time activity updates
      if (activeTab === 'itinerary') {
        window.location.reload(); // Simple refresh for now
      }
    });

    newSocket.on('expense-added', () => {
      if (activeTab === 'expenses' || activeTab === 'balances') {
        window.location.reload();
      }
    });

    fetchTripData();

    return () => {
      window.removeEventListener('online', handleOnline);
      window.removeEventListener('offline', handleOffline);
      newSocket.emit('leave-trip', tripId);
      newSocket.close();
    };
  }, [tripId]);

  const fetchTripData = async () => {
    try {
      const [tripRes, membersRes] = await Promise.all([
        axios.get(`${API_URL}/trips/${tripId}`),
        axios.get(`${API_URL}/trips/${tripId}/members`)
      ]);
      
      setTrip(tripRes.data);
      setMembers(membersRes.data);
    } catch (error) {
      console.error('Error fetching trip data:', error);
      if (error.code === 'ERR_NETWORK') {
        // Try to load from localStorage if offline
        loadFromLocalStorage();
      }
    } finally {
      setLoading(false);
    }
  };

  const loadFromLocalStorage = () => {
    const cached = localStorage.getItem(`trip_${tripId}`);
    if (cached) {
      const data = JSON.parse(cached);
      setTrip(data.trip);
      setMembers(data.members);
    }
  };

  const saveToLocalStorage = (tripData, membersData) => {
    localStorage.setItem(`trip_${tripId}`, JSON.stringify({
      trip: tripData,
      members: membersData,
      timestamp: Date.now()
    }));
  };

  useEffect(() => {
    if (trip && members) {
      saveToLocalStorage(trip, members);
    }
  }, [trip, members]);

  if (loading) {
    return <div className="loading">Loading trip...</div>;
  }

  if (!trip) {
    return <div className="error">Trip not found</div>;
  }

  const isOrganizer = trip.organizer_id === currentUser.id;
  const userMember = members.find(m => m.user_id === currentUser.id);
  const canEdit = isOrganizer || (userMember && userMember.permissions === 'edit');

  return (
    <div className="trip-view">
      <header className="trip-header">
        <div className="container">
          <div className="trip-header-content">
            <div>
              <button onClick={() => navigate('/dashboard')} className="back-btn"> Back</button>
              <h1>{trip.name}</h1>
              <p className="trip-subtitle">{trip.destination || 'No destination set'}</p>
            </div>
            <div className="header-actions">
              {isOffline && <span className="offline-badge">Offline</span>}
              <span className="user-name">{currentUser.name}</span>
              <button onClick={onLogout} className="btn btn-secondary">Logout</button>
            </div>
          </div>
        </div>
      </header>

      <div className="trip-tabs">
        <div className="container">
          <div className="tabs">
            <button
              className={`tab ${activeTab === 'itinerary' ? 'active' : ''}`}
              onClick={() => setActiveTab('itinerary')}
            >
              Itinerary
            </button>
            <button
              className={`tab ${activeTab === 'expenses' ? 'active' : ''}`}
              onClick={() => setActiveTab('expenses')}
            >
              Expenses
            </button>
            <button
              className={`tab ${activeTab === 'balances' ? 'active' : ''}`}
              onClick={() => setActiveTab('balances')}
            >
              Balances
            </button>
            <button
              className={`tab ${activeTab === 'voting' ? 'active' : ''}`}
              onClick={() => setActiveTab('voting')}
            >
              Voting
            </button>
            <button
              className={`tab ${activeTab === 'documents' ? 'active' : ''}`}
              onClick={() => setActiveTab('documents')}
            >
              Documents
            </button>
          </div>
        </div>
      </div>

      <div className="trip-content">
        <div className="container">
          {activeTab === 'itinerary' && (
            <ItineraryTab
              tripId={tripId}
              currentUser={currentUser}
              members={members}
              canEdit={canEdit}
              isOrganizer={isOrganizer}
              socket={socket}
            />
          )}
          {activeTab === 'expenses' && (
            <ExpensesTab
              tripId={tripId}
              currentUser={currentUser}
              members={members}
              canEdit={canEdit}
              socket={socket}
            />
          )}
          {activeTab === 'balances' && (
            <BalancesTab
              tripId={tripId}
              currentUser={currentUser}
              members={members}
            />
          )}
          {activeTab === 'voting' && (
            <VotingTab
              tripId={tripId}
              currentUser={currentUser}
              members={members}
              canEdit={canEdit}
              socket={socket}
            />
          )}
          {activeTab === 'documents' && (
            <DocumentsTab
              tripId={tripId}
              currentUser={currentUser}
              members={members}
              canEdit={canEdit}
            />
          )}
        </div>
      </div>
    </div>
  );
}

export default TripView;

